import React, { useEffect, useState } from "react";
import axios from "axios";
import MultiDayTimeline from "./components/MultiDayTimeline";
import Sidebar from "./components/Sidebar";
import "./App.css";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import CalendarEvent from "./models/CalendarEvent";
import MapWithMarkers from "./components/MapWithMarkers";
import { parseDateTime } from "./utils/eventUtils";

function App() {
  const [apiKey, setApiKey] = useState("");
  const [isApiKeySet, setIsApiKeySet] = useState(false);
  const [events, setEvents] = useState([]);
  const [foodPlaces, setFoodPlaces] = useState([]);
  const [landmarkPlaces, setLandmarkPlaces] = useState([]);
  const [selectedEventId, setSelectedEventId] = useState(null);
  const [selectedFoodId, setSelectedFoodId] = useState(null);
  const [selectedLandmarkId, setSelectedLandmarkId] = useState(null);
  const [selectedDay, setSelectedDay] = useState(null);

  const onAddEvent = (eventData, type) => {
    let eventDate;
    if (selectedDay) {
      eventDate = new Date(selectedDay);
    } else if (events.length > 0) {
      eventDate = new Date(events[0].startTime);
    } else {
      eventDate = new Date();
    }
    const newStartTime = new Date(eventDate);
    newStartTime.setHours(7, 0, 0, 0);

    const newEndTime = new Date(eventDate);
    newEndTime.setHours(8, 0, 0, 0);

    const newEvent = new CalendarEvent({
      id: Date.now(),
      title: eventData.title || "New Event",
      description: eventData.description || "",
      type: type,
      website: eventData.website || "",
      startTime: newStartTime,
      endTime: newEndTime,
      location: eventData.coordinates ? eventData.coordinates.join(",") : "",
      endLocation: "",
      guests: [],
      address: eventData.location || "",
      endAddress: "",
    });

    setEvents((prevEvents) => [...prevEvents, newEvent].sort((a, b) => a.startTime - b.startTime));
  };

  const handleSync = async () => {
    try {
      const response = await fetch("https://synctogooglesheet-4gsosplf7q-uc.a.run.app", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ data: { events } }),
      });

      const result = await response.json();
      if (response.ok && result.success) {
        alert("Data synced successfully!");
      } else {
        alert("Failed to sync data.");
      }
    } catch (error) {
      console.error("Error syncing data:", error);
      alert("Error syncing data. Check console for details.");
    }
  };

  const handleDaySelect = (day) => {
    setSelectedDay(day);
    setSelectedFoodId(null);
    setSelectedLandmarkId(null);
  };

  const handleEventSelect = (eventId) => {
    setSelectedEventId(eventId);
    setSelectedFoodId(null);
    setSelectedLandmarkId(null);
  };

  const handleFoodSelect = (foodId) => {
    setSelectedFoodId(foodId);
    setSelectedLandmarkId(null);
    setSelectedEventId(null);
  };

  const handleLandmarkSelect = (landmarkId) => {
    setSelectedLandmarkId(landmarkId);
    setSelectedFoodId(null);
    setSelectedEventId(null);
  };

  const handleEventUpdate = (updatedEvent) => {
    setEvents((prevEvents) =>
      prevEvents
        .map((event) => (event.id === updatedEvent.id ? { ...event, ...updatedEvent } : event))
        .sort((a, b) => a.startTime - b.startTime)
    );
  };

  const fetchData = async () => {
    const spreadsheetId = "1yTbALjVCCWi9tnW7W2Pq0sZrJAgdJqyCna55PYarfWo";
    const eventsUrl = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/events!A:L?key=${apiKey}`;
    const foodUrl = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/food!A:D?key=${apiKey}`;
    const landmarkUrl = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/landmarks!A:D?key=${apiKey}`;

    try {
      const eventsResponse = await axios.get(eventsUrl);
      const eventRows = eventsResponse.data.values;
      const eventData = eventRows.slice(1).map((row, index) =>
        CalendarEvent.fromRow(row, parseDateTime, index)
      );
      setEvents(eventData);

      const foodResponse = await axios.get(foodUrl);
      const foodRows = foodResponse.data.values;
      const foodData = foodRows.slice(1).map((row) => ({
        title: row[0],
        website: row[1],
        location: row[2],
        coordinates: row[3]?.split(",").map(Number) || null,
      }));
      setFoodPlaces(foodData);

      const landmarkResponse = await axios.get(landmarkUrl);
      const landmarkRows = landmarkResponse.data.values;
      const landmarkData = landmarkRows.slice(1).map((row) => ({
        title: row[0],
        website: row[1],
        location: row[2],
        coordinates: row[3]?.split(",").map(Number) || null,
      }));
      setLandmarkPlaces(landmarkData);
    } catch (error) {
      console.error("Error fetching data", error);
    }
  };

  useEffect(() => {
    if (isApiKeySet) {
      fetchData();
    }
  }, [isApiKeySet]);

  const timelineConfig = {
    dayWidth: "calc(25% - 2px)",
    fontSize: "12px",
    showLabels: true,
  };

  return (
    <div>
      {!isApiKeySet ? (
        <div className="api-key-form">
          <h2>Set API Key</h2>
          <input
            type="text"
            placeholder="Enter API Key"
            value={apiKey}
            onChange={(e) => setApiKey(e.target.value)}
          />
          <button onClick={() => setIsApiKeySet(Boolean(apiKey))}>Submit</button>
        </div>
      ) : (
        <DndProvider backend={HTML5Backend}>
          <div className="main-content">
            <div className="chart-container">
              <MultiDayTimeline
                events={events}
                setEvents={setEvents}
                config={timelineConfig}
                selectedEventId={selectedEventId}
                onEventSelect={handleEventSelect}
                onEventUpdate={handleEventUpdate}
                onSelectDay={handleDaySelect}
                selectedDay={selectedDay}
                apiKey={apiKey}
              />
            </div>
            <div className="sidebar-container">
              <MapWithMarkers
                events={events}
                foodPlaces={foodPlaces}
                landmarkPlaces={landmarkPlaces}
                selectedDay={selectedDay}
                selectedEventId={selectedEventId}
                selectedFoodId={selectedFoodId}
                selectedLandmarkId={selectedLandmarkId}
                onAddEvent={onAddEvent}
                apiKey={apiKey}
                handleSync={handleSync}
              />
            </div>
          </div>
        </DndProvider>
      )}
    </div>
  );
}

export default App;
